      <el-form-item class="input-container" style=" top: 0px; ">
        <input type="text"  v-model="joinForm.code"  required="required" style="z-index:1;" @blur="code"/>
        <label for="code">验证码</label>
        <img class="code-img" :src="codeImg ? codeImg : require('../../assets/img/error.png')" width="110px" height="50px" style="z-index:100;"  @click.stop="joinCode()">
