    // 加载验证图片
    joinCode () {
      axios({
        method: 'get',
        baseURL: baseUrl,
        url: '/getVerifyCode',
        timeout: 30000
      })
        .then(response => {
          this.codeImg = 'data:image/jpg;base64,' + response.data.data
        })
        .catch(error => {
          console.error(error)
        })
    },
    // 点击全部验证后才把表单提交给后台
    async submit (formName) {
      // 点击后防止多次提交
      // 再全部验证一遍
      if (this.nameValidator() === 'pass' && this.telValidator() === 'pass' && await this.numberValidator() === 'pass' &&
       this.codeValidator() === 'pass' && this.reasonValidator() === 'pass') {
        this.realSub() // 提交跳转
      } else {
        this.$notify.error({
          message: '有信息填写错误，无法提交'
        })
      }
    },
    // 真正向后台提交
    realSub () {
      let that = this
      let join = that.joinForm
      axios({
        method: 'post',
        baseURL: baseUrl,
        url: '/addSignUpById',
        // 如果请求的时间超过'timeout'，请求将被中止。
        timeout: 2500,
        data: {
          name: join.name,
          tel: join.tel,
          // college: join.college,
          // profession: join.profession,
          // clazz: join.clazz,
          number: join.number,
          code: join.code,
          reason: join.reason
        }
      }).then(function (response) {
        that.joinCode()
        Object.keys(join).forEach((key) => {
          join[key] = ''
        }
        )
        if (response.data.status === 'success') {
          that.$notify.success({
            message: '报名成功'
          })
          //  提交成功后，跳转到主页
          that.$router.push('/home')
        } else {
          // console.log(response.data.data)
          that.$notify.error({
            message: response.data.data.errMsg
          })
          that.joinCode()
        }
      }).catch(function (error) { // 不能表面的抛出错误吧？
        console.log(error)
      })
    },
    name () {
      var valid = this.nameValidator()
      if (valid !== 'pass') {
        this.$notify.error({
          message: valid
        })
      }
    },
    nameValidator () {
      if (this.joinForm.name.length < 2) {
        return '名字怎么会这么短呢？'
      } else if (!/^[\u4E00-\u9FA5]+.?[\u4E00-\u9FA5]+$/.test(this.joinForm.name)) {
        return '名字有无效字符哈'
      } else {
        return 'pass'
      }
    },
    tel () {
      var valid = this.telValidator()
      if (valid !== 'pass') {
        this.$notify.error({
          message: valid
        })
      }
    },
    telValidator () {
      if (this.joinForm.tel.length === 0) {
        return '请留下你的手机号码哦'
      } else if (!/^1(3|4|5|7|8)\d{9}$/.test(this.joinForm.tel)) {
        return '手机号码写的不大对吧？'
      } else {
        return 'pass'
      }
    },
    // 院系验证
    // college () {
