    //   } else {
    //     return 'pass'
    //   }
    // },
    // 学号验证
    async number () {
      let valid = await this.numberValidator()
      if (valid !== 'pass') {
        this.$notify.error({
          message: valid
        })
      }
    },
    async numberValidator () {
      // 判空 年份 格式
      // axios
      let that = this
      let join = that.joinForm
      let numResult = ''
      await axios({
        method: 'get',
        // baseUrl: baseUrl,
        url: baseUrl + '/verifyNumber',
        // 如果请求的时间超过'timeout'，请求将被中止。
        timeout: 2500,
        params: {
          number: join.number
        }
      }).then(function (response) {
        if (response.data.status === 'fail') {
          let msg = response.data.data.errMsg
          // that.$notify.error({
          //   message: msg
          // })
          numResult = msg
        } else {
          numResult = 'pass'
        }
      }).catch(function (error) { // 不能表面的抛出错误吧？
        console.log(error)
      })
      return numResult
    },
    // 原因验证
    reason () {
      var valid = this.reasonValidator()
      if (valid !== 'pass') {
        this.$notify.error({
          message: valid
        })
      }
    },
    reasonValidator () {
      // 判空
      if (this.joinForm.reason.length === 0) {
        return '请填写原因哦'
      } else {
        return 'pass'
      }
    },
    // 验证码验证
    code () {
      let valid = this.codeValidator()
      if (valid === 'fail') {
        this.$notify.error({
          message: '验证码没有填哦'
        })
      }
    },
    codeValidator () {
      if (this.joinForm.code.length === 0) {
        return 'fail'
      } else {
        return 'pass'
      }
    //   let that = this
    //   let codeResult = ''
    //   await axios({
    //     methods: 'get',
