      baseURL: baseUrl,
      url: '/getSignTime',
      timeout: 30000
    })
      .then(response => {
        console.log(response.data)
        if (response.data.status === 'fail') {
          that.$alert(response.data.data, {
            confirmButtonText: '确定',
            callback: action => {
              that.$message({
                type: 'info',
                message: `已为您返回主页`
              })
            }
          })
          that.$router.push('/home')
        }
