<!-- 照片墙上方分年级的子导航栏 -->
<template>
<div class='main'>
  <el-menu text-color="#949494"
  active-text-color="#000"
  :default-active="activeIndex">
    <el-menu-item index="1">
        <router-link :to="{ params: { time: 2018 }}">2018级</router-link>
    </el-menu-item>
    <el-menu-item index="2">
      <router-link :to="{ params: { time: 2017 }}">2017级</router-link>
    </el-menu-item>
    <el-menu-item index="3">
      <router-link :to="{ params: { time: 2016 }}">2016级</router-link>
    </el-menu-item>
    <el-menu-item index="4">
      <router-link :to="{ params: { time: 2015 }}">2015级</router-link>
    </el-menu-item>
    <!-- <a href="#">2018级</a>
    <a href="#">2017级</a>
    <a href="#">2016级</a>
    <a href="#">2015级</a> -->

  <div id="indicator"></div>
  </el-menu>
</div>
</template>

<script>
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import '组件名称' from '组件路径';

export default {
// import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    // 这里存放数据
    return {
      activeIndex: '1'
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {},
  // 过滤器
  filters: {},
  // 方法集合
  methods: {

  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created () {

  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {

  },
  beforeCreate () {}, // 生命周期 - 创建之前
  beforeMount () {}, // 生命周期 - 挂载之前
  beforeUpdate () {}, // 生命周期 - 更新之前
  updated () {}, // 生命周期 - 更新之后
  beforeDestroy () {}, // 生命周期 - 销毁之前
  destroyed () {}, // 生命周期 - 销毁完成
  activated () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style scoped>
.main
{
    height: 100px;
    min-width: 1510px;
    margin: 0;
    /* background-color: #292929; */
    overflow: hidden;
}

.el-menu
{
    /* color: #949494; */
    position: relative;
    top: 50%;
    right: 0;
    left: 0;
    width: 400px;
    display: table;
    margin: 0 auto;
    transform: translateY(-50%);
    border-right: solid 1px transparent;
}
.el-menu-item.is-active {
    color: transparent;
}
.el-menu-item:focus, .el-menu-item:hover {
    outline: 0;
    background-color: transparent;
}
.el-menu-item
{
    position: relative;
    width: 25%;
    display: table-cell;
    text-align: center;
    color: #949494;
    text-decoration: none;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
    padding: 10px 20px;
    transition: 0.2s ease color;
}
a {
    color: #949494;
}

.el-menu-item:before, .el-menu-item:after
{
    content: "";
    position: absolute;
    border-radius: 50%;
    transform: scale(0);
    transition: 0.2s ease transform;
}

.el-menu-item:before
{
    top: 0;
    left: 10px;
    width: 6px;
    height: 6px;
}

.el-menu-item:after
{
    top: 5px;
    left: 18px;
    width: 4px;
    height: 4px
}

.el-menu-item:nth-child(1):before
{
    background-color: yellow;
}

.el-menu-item:nth-child(1):after
{
    background-color: red;
}

.el-menu-item:nth-child(2):before
{
    background-color: #00e2ff;
}

.el-menu-item:nth-child(2):after
{
    background-color: #89ff00;
}

.el-menu-item:nth-child(3):before
{
    background-color: purple;
}

.el-menu-item:nth-child(3):after
{
    background-color: palevioletred;
}
.el-menu-item:nth-child(4):before
{
    background-color: yellow;
}

.el-menu-item:nth-child(4):after
{
    background-color: olive;
}

#indicator
{
    position: absolute;
    left: 5%;
    bottom: 0;
    width: 30px;
    height: 3px;
    background-color: #fff;
    border-radius: 5px;
    transition: 0.2s ease left;
}

a:hover
{
    color: #000;
}
.el-menu-item:hover:before, .el-menu-item:hover:after
{
    transform: scale(1);
}

.el-menu-item:nth-child(1):hover ~ #indicator
{
    background: linear-gradient(130deg, yellow, red);
}

.el-menu-item:nth-child(2):hover ~ #indicator
{
    left: 34%;
    background: linear-gradient(130deg, #00e2ff, #89ff00);
}

.el-menu-item:nth-child(3):hover ~ #indicator
{
    left: 60%;
    background: linear-gradient(130deg, purple, palevioletred);
}
.el-menu-item:nth-child(4):hover ~ #indicator
{
    left: 90%;
    background: linear-gradient(130deg, yellow, olive);
}
</style>
