<!-- 主页的前三條动态 -->
<template>
<div class='main'>
  <section class="latest-news-events">
      <div class="container">
          <p class="title">Latest News & Events</p>
          <el-row>
            <el-col :span="12"><div class="left"><div class="left-container" v-if="news[0]">
                <img :src='news[0].image' alt="" width="100%">
                <h2><a href="javascript:;" id='link' @click="enterNews(0)">{{news[0].title}}</a></h2>
                <p class="time"> {{news[0].time}} </p>
            </div>
                </div></el-col>
            <el-col :span="12"><div class="right">
                <div class="left-container-top">
                  <el-row v-if="news[1]">
                    <el-col :span="11"><div class="limg"><img :src="news[1].image" width="100%" height="100%"></div></el-col>
                    <el-col :span="13"><div class="rtext" >
                        <!-- 之后news改为一个数组 -->
                      <p class="time"> {{news[1].time}} </p>
                      <h2><a id='link' href="javascript:;" @click="enterNews(1)">{{news[1].title}}</a></h2>
                      <p class="brief">{{news[1].brief}}</p>
                        </div></el-col>
                  </el-row>
                </div>
                <div class="left-container-bottom"><el-row v-if="news[2]">
                    <el-col :span="11"><div class="limg"><img :src="news[2].image" width="100%" height="100%"></div></el-col>
                    <el-col :span="13"><div class="rtext" >
                      <p class="time"> {{news[2].time}} </p>
                      <h2><a href="javascript:;" id='link' @click="enterNews(2)">{{news[2].title}}</a></h2>
                      <p class="brief">{{news[2].brief}}</p>
                        </div></el-col>
                  </el-row></div></div></el-col>
          </el-row>
        </div><!-- .container -->
    </section>
</div>
</template>

<script>
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import '组件名称' from '组件路径';
import { baseUrl } from '../../assets/js/util.js'
import axios from 'axios'
axios.defaults.withCredentials = true

export default {
// import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    // 这里存放数据
    return {
      news: [
        // {
        //   title: 'Personalized online learning experience',
        //   time: '22 MAR 2018',
        //   brief: 'Ms. Lara Croft 02 Comments Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt.',
        //   image: '../../assets/img/note1.jpg'
        // },
        // {
        //   title: 'Personalized online learning experience',
        //   time: '22 MAR 2018',
        //   brief: 'Ms. Lara Croft 02 Comments Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt.',
        //   image: '../../assets/img/note3.jpg'
        // },
        // {
        //   title: 'Personalized online learning experience',
        //   time: '22 MAR 2018',
        //   brief: 'Ms. Lara Croft 02 Comments Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt.',
        //   image: '../../assets/img/note3.jpg'
        // }
      ]
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {},
  // 过滤器
  filters: {},
  // 方法集合
  methods: {
    enterNews () {
      this.$router.push('/')
    },
    getNews () {
      let that = this
      axios({
        method: 'get',
        baseURL: baseUrl,
        url: '/getDynamicStateByType',
        // 如果请求的时间超过'timeout'，请求将被中止。
        timeout: 30000,
        params: {
          type: 'news'
        }
      }).then(function (response) {
        if (response.data.status === 'success') {
          // console.log(response.data.data)
          that.news = response.data.data.slice(0, 3)
          // console.log(that.news)
          that.news.forEach((element, index) => {
            element.image = 'data:image/jpg;base64,' + element.image
          })
          // console.log(that.news)
        }
      }).catch(function (error) { // 不能表面的抛出错误吧？
        console.log(error)
      })
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.getNews()
  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    // this.getNews()
  },
  beforeCreate () {}, // 生命周期 - 创建之前
  beforeMount () {}, // 生命周期 - 挂载之前
  beforeUpdate () {}, // 生命周期 - 更新之前
  updated () {}, // 生命周期 - 更新之后
  beforeDestroy () {}, // 生命周期 - 销毁之前
  destroyed () {}, // 生命周期 - 销毁完成
  activated () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style scoped>
section {
    /* 避免双滚动条的问题 */
    overflow: hidden;
    margin: 0 50px 0 50px;
    height: 570px;
    width: auto;
}
.title {
    /* vertical-align: middle; */
    /* margin: 0 auto; */
    text-align: center;
    margin-bottom: 30px;
    font-size: 35px;
    font-weight: 600;
    /* font-family: Verdana, Geneva, Tahoma, sans-serif; */
    font-family: heebo,sans-serif;
}
/* .container {

} */
.left {
    height: 500px;
    /* background: olive; */

}
.right {
    height: 500px;
}
.left-container {
    margin: 0 40px 0 80px;
    /* border: solid 1px black; */
}
.left-container img { height: 400px;
}
.left-container-top {
    /* border: solid 1px black; */
    margin: 0 40px 0 40px;
    height: 230px;
}
.left-container-bottom {
    /* border: solid 1px black; */
    margin: 20px 40px 0 40px;
    height: 230px;
}
h2 {margin:20px 0 20px 0;}
#link {
    text-decoration: none;
    /* out-line: none; */
    background-color: transparent;
    color: #383749;
    touch-action: manipulation
  }
/* #link :visited {

} */
#link:hover {
    color: #757686;
    text-decoration: underline;
}
.limg {
    height: 230px;
    /* background: olive; */
    margin: 0 40px 0 0;
}
.rtext {
    padding: 15px 0 0 0;
    height: 230px;
    /* background: olivedrab; */
}
</style>
