<!-- blog -->
<template>
<div class='main'>
  <h3>From us</h3>
  <h1>more blogs</h1>
  <el-row :gutter="30">
    <!-- :gutter="40" -->
    <el-col class="grid" :span="8" v-for="(item, index) in blogs" :key="index">
      <div class="entry-media">
        <img :src="item.image" alt="" width="100%"  height="296px">
      </div>
      <div class="details">
        <h3><a href="javascript:;" @click="enterPassage(index)">{{item.title}}</a></h3>
        <p>{{item.brief}}</p>
        <ul class="entry-meta">
          <li><el-avatar icon="el-icon-user-solid"></el-avatar></li>
          <li>By <a href="javascript:;">{{item.writer}}</a></li>
          <li>|</li>
          <li>{{item.time}}</li>
        </ul>
      </div>
    </el-col>
  </el-row>
</div>
</template>

<script>
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import '组件名称' from '组件路径';
import { baseUrl } from '../../assets/js/util.js'
import axios from 'axios'
axios.defaults.withCredentials = true

export default {
// import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    // 这里存放数据
    return {
      blogs: [
        // {
        //   title: 'Everything is easy when you think it easy',
        //   brief: 'There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration.',
        //   writer: 'Lily Anne',
        //   time: 'Octobor 12,2019',
        //   image: ''
        // }, {
        //   title: 'Everything is easy when you think it easy',
        //   brief: 'There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration.',
        //   writer: 'Lily Anne',
        //   time: 'Octobor 12,2019',
        //   image: ''
        // }, {
        //   title: 'Everything is easy when you think it easy',
        //   brief: 'There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration.',
        //   writer: 'Lily Anne',
        //   time: 'Octobor 12,2019',
        //   image: ''
        // }
      ]
    }
  },
  // 监听属性 类似于data概念
  computed: {

  },
  // 监控data中的数据变化
  watch: {},
  // 过滤器
  filters: {},
  // 方法集合
  methods: {
    enterPassage (index) {
      this.$router.push('/')
    },
    getPassages () {
      let that = this
      axios({
        method: 'get',
        baseURL: baseUrl,
        url: '/getDynamicStateByType',
        // 如果请求的时间超过'timeout'，请求将被中止。
        timeout: 30000,
        params: {
          type: 'blogs'
        }
      }).then(function (response) {
        if (response.data.status === 'success') {
          that.blogs = response.data.data.slice(0, 3)
          that.blogs.forEach((element, index) => {
            element.image = 'data:image/jpg;base64,' + element.image
          })
        }
      }).catch(function (error) { // 不能表面的抛出错误吧？
        console.log(error)
      })
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.getPassages()
  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {

  },
  beforeCreate () {}, // 生命周期 - 创建之前
  beforeMount () {}, // 生命周期 - 挂载之前
  beforeUpdate () {}, // 生命周期 - 更新之前
  updated () {}, // 生命周期 - 更新之后
  beforeDestroy () {}, // 生命周期 - 销毁之前
  destroyed () {}, // 生命周期 - 销毁完成
  activated () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style scoped>
.main {
  width:auto;
  height: 700px;
  /* margin: 50px 50px 0 50px; */
  margin: 50px 100px 0 100px;
  overflow: hidden;
  padding: 0 50px 0 50px;
}
.grid {
    /* background: olive; */
}
h1 {
    font-weight: 600;
    font-size: 35px;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 30px;

}
h3 {
    text-align: center;
    font-family: heebo,sans-serif;
    font-size: 1rem;
    /* color: #ddd; */
}
.details {
    padding: 35px 30px;
    background: #f7f4ef;
    height: 270px;
}
.entry-media {
  height:296px;
}
.details h3 {
    font-size: 22px;
    font-size: 1.375rem;
    line-height: 1.4em;
    margin: 0 0 .7em;
    font-weight: 700;
    text-align: left;
}
a {
  text-decoration: none;
  /* out-line: none; */
  background-color: transparent;
  color: #383749;
  touch-action: manipulation
}
a:hover {
    color: #757686;
    text-decoration: underline;
}
p {
  margin: 0 0 10px;
}
ul {
  padding-left: 0;
  margin: 0;
  text-decoration: none;
  overflow: hidden;
  padding-top: 10px;
  line-height: 55px;
  }
li {
  margin-right: 15px;
  display: list-item;
  text-align: -webkit-match-parent;
  list-style: none;
  float:left;
  padding-top: 12px;
  font-size: 15px;
  font-family: heebo,sans-serif;
}
</style>
