import Vue from 'vue'
import Router from 'vue-router'

import joinUsView from './views/joinUsGuide/joinUsView'
import introView from './views/introGuide/introView'
import ourDynamics from './views/dynamicsGuide/ourDynamics'
import photoWallView from './views/photoWallGuide/photoWall'
import forbidden from './views/errorGuide/403'
import notFound from './views/errorGuide/404'
import home from './views/home.vue'
Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/joinUs',
      component: joinUsView
    },
    {
      path: '/photoWall/:time',
      component: photoWallView
    },
    {
      path: '/ourDynamics/:type',
      component: ourDynamics
    },
    {
      path: '/intro',
      component: introView
    },
    {
      path: '/403',
      component: forbidden
    },
    {
      path: '*',
      component: notFound
    }
  ]
})
