      showLoading()
      console.log(time)
      let that = this
      that.list = []
      axios({
        methods: 'get',
        baseURL: baseUrl,
        url: '/getMemberFormGrade',
        timeout: 60000,
        params: {
          // time
          grade: time
        }
      }).then((response) => {
        hideLoading()
        console.log(response.data)
        if (response.data.status === 'success') {
          that.prepare = response.data.data
          that.prepare.forEach((element, index) => {
            element.image = 'data:image/jpg;base64,' + element.image
          })
          that.list = that.createList(0) // 加入list中
        }
      }).catch((error) => {
        console.log(error)
      })
    },
