<!-- 导航栏 和 页脚 -->
<template>
  <div class="exterior">
    <el-container>
      <el-header  v-if="show">
        <app-header></app-header>
      </el-header>
      <el-main>
        <router-view></router-view>
      </el-main>
      <el-footer  v-if="show">
        <app-footer></app-footer>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import '组件名称' from '组件路径';
import header from '../components/headerGuide/header'
import footer from '../components/footerGuide/footer'
export default {
// import引入的组件需要注入到对象中才能使用
  components: {
    'app-header': header,
    'app-footer': footer
  },
  data () {
    // 这里存放数据
    return {
      show: true
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {
    $route (to, from) {
      console.log(this.$route.path.slice(1))
      if (this.$route.path.slice(1) === '403') {
        this.show = false
      } else {
        this.show = true
      }
    }
  },
  // 过滤器
  filters: {},
  // 方法集合
  methods: {
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created () {
    if (this.$route.path.slice(1) === 'error') {
      this.show = false
    } else {
      this.show = true
    }
  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {

  },
  beforeCreate () {}, // 生命周期 - 创建之前
  beforeMount () {}, // 生命周期 - 挂载之前
  beforeUpdate () {}, // 生命周期 - 更新之前
  updated () {}, // 生命周期 - 更新之后
  beforeDestroy () {}, // 生命周期 - 销毁之前
  destroyed () {}, // 生命周期 - 销毁完成
  activated () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style scoped>
#app .el-header,
#app .el-main,
#app .el-footer {
  padding: 0;
}
.content {
  min-width: 1200px;
  margin: 0 auto;
}
