<!-- 轮播图 -->
<template>
<div class='main'>
   <el-carousel :interval="4000" arrow="never"  ref="swiperWrap" :style='`width: 100%; height: ${config.height}px;`' autoplay>
    <el-carousel-item v-for="item in images" :key="item.id" :style='`width: 100%; height: ${config.height}px;`'>
      <img :src="item.idView?item.idView:require('../../assets/img/noimg.jpg')" alt="" class="img-div" :style='` height: 100%; width: 100%; background-size: cover;`'>
    </el-carousel-item>
  </el-carousel>
</div>
</template>

<script>
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import '组件名称' from '组件路径';
import { showLoading, hideLoading } from '../../assets/js/loading'

export default {
// import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    // 这里存放数据
    return {
      images: [
        { id: 0, idView: require('../../assets/img/rotation3.jpg') },
        { id: 1, idView: require('../../assets/img/rotation1.png') },
        { id: 2, idView: require('../../assets/img/rotation4.jpg') }
      ],
      // imgHeight: '',
      config: {
        height: ''
      }
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {},
  // 过滤器
  filters: {},
  // 方法集合
  methods: {
    resetHeight () {
      // const height = window.screen.height - 60
      const height = window.screen.availHeight - 160
      // console.log(height)
      this.config.height = height
    }
  },
  beforeCreate () {
    showLoading()
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    // this.imgHeight = document.body.clientHeight
    hideLoading()
    const $this = this
    this.resetHeight()
    window.addEventListener('resize', () => {
      $this.resetHeight()
    })
  },
  beforeMount () {}, // 生命周期 - 挂载之前
  beforeUpdate () {}, // 生命周期 - 更新之前
  updated () {}, // 生命周期 - 更新之后
  beforeDestroy () {}, // 生命周期 - 销毁之前
  destroyed () {}, // 生命周期 - 销毁完成
  activated () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style scoped>
  .img-div{
    width:100%;
    /* height:400px; */
  }
</style>
