  // 监听属性 类似于data概念
  computed: {},
  beforeRouteUpdate (to, from, next) {
    // 在当前路由改变，但是该组件被复用时调用
    // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
    // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
    // 可以访问组件实例 `this`
    // console.log(to)
    // console.log(from)

    let type = to.params.type
    // console.log('路由守卫：' + type)
    this.getClassify(type)
    next() // 导航被确认的时候执行回调
  },
  // 监控data中的数据变化
  watch: {
    // `this.$route.params.type`: {
    //   this.getClassify()
    // }
  },
  // 过滤器
