<script>
import defaultView from './views/defaultView'
import axios from 'axios'
axios.defaults.withCredentials = true
export default {
  name: 'app',
  components: {
    defaultView
  },
  data () {
    // 这里存放数据
    return {
    }
  },
  created () {
    let that = this
    axios.interceptors.response.use(function (response) {
    // 在接收响应做些什么，例如跳转到登录
      return response
    }, function (error) {
    // 对响应错误做点什么
      that.$router.push('/403')
      return Promise.reject(error)
    })
  }
}
</script>

